@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700&display=swap);

/* menu */
.img1 {
  width: 100px;
  height: 100px;
}

.menuContainer {
  width: 200px;
  height: 100vh;
  text-align: center;
  padding: 25px 5px 0 5px;
}

.myTitle {
  margin: 10px 0 25px;
  font-size: 1.5rem;
}
.myTitle a {
  text-decoration: none;
  color: #000000;
}
.myTitle a:hover {
  text-decoration: none;
  color: #000000;
}
.link {
  display: block;
  padding-top: 12px;
  padding-bottom: 12px;
}

.menuItems .link:hover {
  border-right: 2px solid #0099e5;
}
.menuItems a {
  text-decoration: none;
  color: #000000;
}
.menuItems a:hover {
  text-decoration: none;
  color: #000000;
}

.menuSocials {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 50px;
  font-size: 20px;
}
.menuSocials li {
  padding-left: 15px;
}
.menuSocials li:first-child {
  padding-left: 0;
}
.menuSocials a {
  text-decoration: none;
  color: #000000;
}
.menuSocials a:hover {
  text-decoration: none;
  color: #000000;
}

.icons {
  color: #666666;
}

li {
  list-style-type: none;
}

/* Mobile menu */
.imgMobile {
  width: 35px;
  height: 35px;
}

.mobileMenu {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.mobileMenuContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 15px;
  padding-right: 15px;
  height: 50px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 111111;
  background-color: #ffffff;
  width: 100%;
}

.mobileMenuContainer ._button {
  background-color: transparent;
  border: none;
  outline: none;
  font-size: 20px;
}

.menuMargin {
  margin-bottom: 50px;
}

.myTitleMobile {
  margin-left: 10px;
  font-size: 1.5rem;
}
.myTitleMobile a {
  text-decoration: none;
  color: #000000;
}
.myTitleMobile a:hover {
  text-decoration: none;
  color: #000000;
}

.toggleMenu {
  width: 300px;
  height: 100vh;
  text-align: center;
  padding: 25px 5px 0 5px;
  position: fixed;
  z-index: 1111;
  background-color: white;
  top: 50px;
  left: 0;
}

/* App */

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Poppins", Helvetica, sans-serif;
  }
 
  html {
    font-size: 14px;
  }

  p{
    font-size: 1rem;
    font-weight: 400;
    color: #666666;
  }
  
.containerPage{
    display: flex;
    flex-direction: row;  
}

.display{
    display: none;
}

.pageTitle{
    border-bottom: 2px solid #e0e0e0;
    margin-bottom: 3rem;
}

._container{
    padding-top: 50px;
}

/* Home page */
.imgContainer{
    width: 100%;
    height: 100%;
}

.paddingZero{
    padding: 0!important;
}

.imgHome{
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}
.homeAboutContainer div{
    padding: 4rem 6rem 1rem 6rem;
}
.homeAboutContainer h3{
    font-size: 2.4rem;
    padding:0.4rem 0 0.4rem 0;
}
.myTitles::before{
    content:'Webmaster';
	color: black;
	-webkit-animation: text infinite 6s;
	        animation: text infinite 6s;
}
@-webkit-keyframes text {
	
	33%{content:'Front end Developer';}
	66%{content:'React Developer';}
	
}
@keyframes text {
	
	33%{content:'Front end Developer';}
	66%{content:'React Developer';}
	
}
.myJobContainer{
    padding:  3rem 3rem 3rem 0;
}
._title{
    margin-bottom: 2rem;
}
._titleİn{ 
    font-size: 1.5rem;
    border-bottom: 2px solid #0099e5;
}

.homeAboutContainer div button{
    border:none;
    border: 2px solid #0099e5;
    padding: 5px 10px 5px 10px;
    border-radius: 5px 0 5px 0;
    outline:none;
}
.homeAboutContainer div button:hover{
    
    border: 2px solid #0099e5;
    color: #ffffff;
    background-color: #0099e5;
    
}

.items{ 
   display: flex;
   flex-direction: row;
   margin-bottom: 2rem;
}
.items-svg{ 
    
    height: 3rem;
    margin-right: 25px;

 }

/* About page */

._right{
    text-align: right;
}


@media only screen and (max-width: 1140px) {
    .homeAboutContainer div{
        padding: 4rem 3rem 1rem 3rem;
    }
  }

  @media only screen and (max-width: 998px) {
    .containerPage{
        display: flex;
        flex-direction: column;
        
    }
    .homeAboutContainer div{
        padding: 1rem 1rem 0 1rem;
    }
  }

  @media only screen and (max-width: 767px) {
       
    .homeAboutContainer div{
        padding: 1rem 0 0 0;
    }
    .homeAboutContainer h3{
        font-size: 2.5rem;
    }
    .myJobContainer{
        padding: 3rem 0 3rem 0;
    }
  }

  @media only screen and (max-width: 575px) {
       
    ._right{
        text-align: left;
    }
  }



  
/*  Skilss */
