    border-radius: 10px;
}
.my-all-skill{
    margin: 1rem 3rem 2rem 2rem;
}
.my-skill{
    margin-bottom: 0.75rem;
}

.skill{
    display: flex;
    justify-content: space-between;
}
.skill-bar .skill-in-all{
height: 4px;
    background-color: #0099e5;
    margin-top: 2px;
    margin-left: 2px;
}

