}

/* İletişim */

._card{
    border: 1px solid rgb(236, 236, 236);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 2rem;
    margin: 10px;
    transition: all .3s;
    
}

._card:hover{
   box-shadow: 0px 2px 3px gray;
   transform: translate(0px, -10px);    
}

.card-svg{
    height: 5rem;
    padding-bottom: 20px;
}

/* Portfolio */

.portfolio-svg{
    height: 10rem;
    margin-bottom: 20px;
 
}


.portfolio-container{
    text-align: center;
    transition: all .3s;
    margin-bottom: 4rem;
 
}

.portfolio-container a{
    color: black;
 
}
.portfolio-container a:hover{
    color: black;
    text-decoration: none;
}

.portfolio-container:hover{
    text-align: center;
    transform: translate(0px, -10px);    
}
